

















































import Vue from "vue";
import Component from "vue-class-component";
import Stairs from "../components/stairs.vue";
import Problem from "../components/problems.vue";
import { Prop } from "vue-property-decorator";
import { recordRemainTime, ParseQuery } from "@/utils";
@Component({
  components: {
    Stairs,
    Problem,
  },
})
export default class Home extends Vue {
  @Prop()
  leave1!: string;
  @Prop()
  leave2!: string;
  isNew = false;
  loading = true;
  chooseNew(b: boolean) {
    this.$emit("newOrOld", b);
    this.$router.push("/one");
  }
  isGender = false;
  chooseGender(b: boolean) {
    this.isGender = false;
    this.isNew = true;
    this.$emit("genderChoose", b);
  }
  goback() {
    this.isGender = true;
    this.isNew = false;
  }
  enterTime = 0;
  leaveTime = 0;
  created() {
    this.$nextTick(() => {
      const process = document.querySelector(".loading_process") as HTMLElement;
      let i = 0;
      setInterval(() => {
        if (i <= 100) process.innerHTML = i++ + "%";
      }, 40);
    });
    setTimeout(() => {
      this.loading = false;
    }, 4000);
    this.enterTime = new Date().getTime();
    window.addEventListener("beforeunload", this.leaveHandler);
  }
  beforeDestroy() {
    /* 停留时间 */
    this.leaveTime = new Date().getTime();
    const remain = (this.leaveTime - this.enterTime) / 1000;
    recordRemainTime({
      id: 0,
      time: remain,
    });
  }
  async leaveHandler() {
    this.leaveTime = new Date().getTime();
    const remain = (this.leaveTime - this.enterTime) / 1000;
    const data = { id: 0, time: remain };
    window.navigator.sendBeacon("/api" + ParseQuery(data));
  }
  destroyed() {
    window.removeEventListener("beforeunload", this.leaveHandler);
  }
  isClick = false;
  clickWrap(ev: Event) {
    const event = ev || window.event;
    const target = event.target as HTMLElement;
    const mask = document.querySelector(".btn_mask");
    if (target?.className !== "btn_mask") {
      if (this.isClick) return;
      mask?.classList.add("flashBlue");
      this.isClick = true;
      setTimeout(() => {
        mask?.classList.remove("flashBlue");
        this.isClick = false;
      }, 1000);
    } else {
      // this.$router.push("/gender");
      this.isGender = true;
    }
  }
}
